@import url(https://fonts.googleapis.com/css2?family=Syne&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: 'Syne';
}
body{
    background: url(/fitness-react/static/media/bg.8a9eb24b.jpg);
    background-size: cover;
}
ul li{
  list-style: none;
}
marquee{
  color: red;
}
input{
  padding: 5px;
}
button{
  padding: 10px;
  width: 100px;
  background: #000;
  border: 2px solid black;
  color: #fff;
}
button:hover{
  background: black;
  color: #fff;
}
button:active{
  transform: scale(0.95);
}
.table tr td{
  padding-top: 15px;
  padding-left: 10px;
  padding-right: 50px;
}

.header{
    text-align: center;
    font-size: 3.2rem;
    text-shadow: 5px 5px 3px #e0e0e0;
    background: transparent;
    border-radius: 10px;
    font-family: 'Lobster', cursive;
}
.header > ul{
    display: flex;
    margin: 0;
    padding: 0;
    justify-content: center;
}
.header > ul > li > a > i{
    font-size: 2.5rem;
    color: #252525;
    padding-right: 20px;
    margin: 0px;
}
.randomquote{
        font-size: 1.7rem;
        font-weight: 700;
        text-align: center;
        color: #252525;
}
.main{
    box-shadow: 0px 3px 3px 2px #c0c0c0;
    padding: 35px 10px;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.main > input{
    padding: 0px 15px;
}
.main > h3{
    font-size: 1.1rem;
}
.display-div{
    margin: 20px auto;
    padding: 5px 15px;
    width: 55%;
    background-color: cadetblue;
    box-shadow: 0px 3px 3px 2px #c0c0c0;
}
.track-div > h2{
    text-align: center;
}
.track-div {
    position: relative;
    margin: 20px auto;
    padding: 5px 15px;
    width: 50%;
    box-shadow: 0px 3px 3px 2px #c0c0c0;
    
}
.track-div > div{
    display: flex;
    justify-content: center;
    font-weight: 700;
    font-size: 1.1rem;
}

.diet-plan-div{
    margin: 20px auto;
    padding: 5px 15px;
    width: 55%;
    box-shadow: 0px 3px 3px 2px #c0c0c0;
    color: #fff;
}
.diet-plan-div > h2{
    text-align: center;
}
.footer{
background: #000;
text-align: center;
color: #fff;
padding: 15px;
margin: 0px;
font-size: 1.2em;
}
